}

.symbol{
    background-color: initial;
    border: initial;
}

.headerbtn{
    @extend .symbol;
    font-weight: 700;
    color: aliceblue;
}

.coinmainDiv{
    @media screen and (min-width:768px){
    display:Grid;
    grid-template-columns: 35% 65%;
    grid-gap: 1rem;
    height:max-content;
    margin: 15rem;
    }
