.coinName{
    
    font-size: $fsize+5px;
  

}

.coinSym{
    
    font-size: $fsize - 10px;
    color: grey;
    margin-top: 15px;
}

.coinicon{
    width:100px;
    margin: auto 25px auto 30px;
}

.namesDiv{
   // display:flex;
   // margin: auto;
    
}

.metaList{
    list-style-type: none;
    padding-left: 0px;
    font-size: 20px;
}

.hash{
    
    font-size: 20px;
    margin: 0px -7px 0px 15px;
    color: #bd1c24;
}

.price{
    font-size: 35px;
    color:#b7c5c5ed;
    margin: auto;
}

.metaTable{
    margin: auto;
}

.metaTableHeader{
    background: initial;

}

.metaTableData{
    font-weight: 500;
}

.priceData{
    margin: auto;
    @media screen and (min-width:768px){
    
        display:flex;
       
    }
}

.successval{
    @extend .successs;
    font-size: 22px;
    margin: auto 0px auto 10px;
}

.failureval{
    @extend .successval;
    color: red;

}

.metaData{
    background: initial;
    border: none;
    padding-left: 2px;

    @media screen and (min-width:768px){
       display: inline-flex;
    }
}

.pagination{
    display: flex;
    justify-content: center;
}

.pagebtn{
    margin: 10px;
}

.listtext{
    font-size: 20px;
    color: #b4b6d4;
}

.coinData{
    display: inline-flex;

}

